import React, { useEffect, useState } from 'react';
import Form from '../general/Form.jsx';
import { Link } from 'react-router-dom';

const initInputs = [
    { type: 'text', name: 'email', label: '', validation: ['required', 'email'] },
    { type: 'text', name: 'fullName', label: ' ', validation: ['required', 'langAndMin2Char', 'twoWords'] },
    { type: 'tel', name: 'phone', label: ' ', validation: ['required', 'phone'] },
    { type: 'text', name: 'city', label: ' / ', validation: ['required', 'langAndMin2Char'] },
    { type: 'text', name: 'street', label: '', validation: ['required'] },
    { type: 'number', name: 'number', label: ' ', validation: ['required'] },
    { type: 'number', name: 'apartment', label: '' },
    { type: 'text', name: 'postal', label: '', validation: ['required'] },
    { type: 'text', name: 'mailbox', label: '.' },
    { type: 'textarea', name: 'notes', label: '' }
];

export default function OrderForm({ user, logout, updateForm }) {

    const [updatedInputs, setUpdatedInputs] = useState(null);
    const [inputs] = useState(initInputs);

    useEffect(() => {
        if (user) {
            const userInputs = inputs.map(input => {
                let inputCopy = { ...input };
                for (let [key, value] of Object.entries(user)) {
                    if (inputCopy.name === key) {
                        inputCopy.value = value;
                    }
                    if (inputCopy.name === 'email') {
                        delete inputCopy.validation;
                        inputCopy.disabled = true;
                    }
                }
                return inputCopy;
            })
            setUpdatedInputs(userInputs);
        } else {
            setUpdatedInputs(inputs);
        }
    }, [user,inputs])

    const onLogOut = () => {
        logout(window.location.href);
        updateForm(false, null);
    }


    if (!updatedInputs) return null;
    return (
        <div className="order-form">
            <h1> </h1>
            {user ? <div className="checkout-auth">{` ${user.fullName}?`} <span onClick={onLogOut}></span></div> :
                <div className="checkout-auth">/ ? <Link to={{ pathname: 'auth/login', lastRoute: window.location.hash }}>/ </Link></div>}
            <Form inputs={updatedInputs} updateForm={updateForm} />
        </div>
    )
}
