import React from 'react';
import OrderProductList from './OrderProductList';

export default function OrderPreview({ order }) {
    const { date, delivery, totalAmount, orderNumber, products } = order

    return (
        <div className="order-preview">
            <div className="order-header flex align-center">
                <div className="date">  : {new Date(date).toLocaleDateString()} - {new Date(date).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</div>
                <div className="order-number"> : {orderNumber}</div>
            </div>
            <OrderProductList products={products} />
            <div className="order-footer flex justify-between align-center">
                <div className="delivery"> : {delivery.method}</div>
                <div className="total-amount">": {totalAmount}<i className="fas fa-shekel-sign"></i></div>
            </div>
        </div>
    )

}
