import React, { useState } from 'react'

import ProductQuantity from './ProductQuantity.jsx';
import { Link } from 'react-router-dom';

export default function ProductContent({ setGrowl, addToBag, product }) {

    const [quantity, setQuantity] = useState(1);
    const min = 1;
    const max = 5;

    const changeQuantity = (diff) => {
        const newQuantity = quantity + diff;
        if (newQuantity >= min && newQuantity <= max) {
            setQuantity(newQuantity)
        }
    }

    const onAddToBag = () => {
        if (!product.inStock) return;
        addToBag({ product, quantity });
        setGrowl(quantity > 1 ? `${quantity}   ` : `  `, 'info')
    }

    const { title, price, inStock } = product;
    const ConditionalLink = inStock ? Link : 'div';

    return (
        <div className="product-content">
            <div className="top-content flex column justify-between">
                <h1 className="content-title flex column">{title}</h1>
                {quantity === 1 || <span> "  {quantity} :</span>}
                <div className="info-continer flex justify-between">
                    <div className="in-stock">{inStock ? '' : ' '}</div>
                    <div className="content-price">{price * quantity}<i className="fas fa-shekel-sign"></i></div>
                </div>
            </div>

            <ProductQuantity quantity={quantity} changeQuantity={changeQuantity} />
            <div className="buy-container flex justify-between align-center">
                <button className={`main-btn secondary ${inStock ? '' : 'disabled'}`} onClick={() => onAddToBag()} > </button>
                <ConditionalLink to={'/checkout'} className="buy-now-container"><button className={`main-btn primary ${inStock ? '' : 'disabled'}`} onClick={() => onAddToBag()}> </button></ConditionalLink>
            </div>
        </div>

    )
}

