import React, { useState, useEffect } from 'react';
import Form from '../general/Form.jsx';

const initInputs = [
    { type: 'text', name: 'email', label: '', disabled: true },
    { type: 'text', name: 'fullName', label: ' ', validation: ['required', 'langAndMin2Char', 'twoWords'] },
    { type: 'text', name: 'city', label: ' / ', validation: ['langAndMin2Char'] },
    { type: 'text', name: 'street', label: '' },
    { type: 'number', name: 'number', label: ' ' },
    { type: 'number', name: 'apartment', label: '' },
    { type: 'text', name: 'postal', label: '' },
    { type: 'text', name: 'mailbox', label: '.' },
    { type: 'tel', name: 'phone', label: ' ', validation: ['phone'] }
];

export default function Edit({ user, updateUser, setGrowl }) {

    const [inputs] = useState(initInputs);
    const [isValid, setIsValid] = useState(null);
    const [form, setForm] = useState(null);
    const [updatedInputs, setUpdatedInputs] = useState(null);

    useEffect(() => {
        const mappedInputs = inputs.map(input => {
            for (let [key, value] of Object.entries(user)) {
                if (input.name === key) {
                    input.value = value;
                }
            }

            return input;
        })
        setUpdatedInputs(mappedInputs)
    }, [inputs, user])

    const updateForm = (isValid, form) => {
        setIsValid(isValid)
        setForm(form)
    }

    const checkIfFormChanged = (form, user) => {
        for (const [key, value] of Object.entries(form)) {
            if (user[key] !== value) {
                return true;
            }
        }
        setGrowl('   ', 'warn')
        return false;
    }

    const onUpdateUser = () => {
        const isChanged = checkIfFormChanged(form, user)

        if (isValid && isChanged) {
            form._id = user._id
            updateUser(form);
        }
    }

    if (!updatedInputs) return null;
    return (
        <div className="flex column align-center">
            <Form inputs={updatedInputs} updateForm={updateForm} />
            <button className={isValid ? "main-btn primary" : "main-btn primary disabled"} onClick={onUpdateUser}> </button>
        </div>
    )

}
